






















import HeadTtop from '../../../../components/head/head.vue'

export default {
	components: {
		'HeadTtop': HeadTtop
	},
	data() {             
		return {
			Title: '意见反馈',
			backshow: true,
			topsrc: '../../../../static/images/back.png',
			type:['功能问题','商品问题','其它问题'],
			typeindex:0,
			textarea:''
		}
	},
	methods:{
		typeClick(index){
			this.typeindex = index
		},
		sureClick(){
			let params = {
				type: this.typeindex + 1,
				content: this.textarea,
				_token: uni.getStorageSync('userinfo')._token
			}
			
			this.$http.HttpRequst.request(false, 'index/opinion', params, 'POST', res => {
				console.log( res )
				uni.showToast({
					title: res.msg,
					duration: 1000,
					success() {
						setTimeout(function() {
							uni.navigateBack();
						}, 1000)
					}
				});
			});
		}
	}
}
